import * as React from 'react';
import * as ReactDOM from 'react-dom';
import ViewAutoPlay from './lib/ViewAutoPlay';
import { QWebChannel } from './lib/qwebchannel';

import defPic from './resources/8c8deae104883d4101a657c7b78d306.jpg';
import defPic2 from './resources/984877d48620c2db65150b10511ecb9.jpg';
import defPic3 from './resources/d6c0930017ef64ae4fc6fe9e17a842d.jpg';

window.onload = function() {
  new QWebChannel(qt.webChannelTransport, function (channel) {
    window.bridge = channel.objects.bridge;

    bridge.showPic.connect(function(files) {
//      console.log(files)
      ReactDOM.render(
        <ViewAutoPlay imgArray={JSON.parse(files)}/>,
        document.getElementById('view')
      );
    });

  ReactDOM.render(
    <ViewAutoPlay imgArray={[defPic, defPic2, defPic3]}/>,
    document.getElementById('view')
  );

//    ReactDOM.render(
//      <img src={defPic}/>,
//      document.getElementById('view')
//    );

    bridge.uiLoaded();
  });
}