import React from 'react';
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import Pagination from './Pagination';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);

class ViewAutoPlay extends React.Component {
  propTypes = {
    imgArray: PropTypes.array.isRequired,
  };

  state = {
    index: 0,
  };

  handleChangeIndex = index => {
    this.setState({
      index,
    });
  };

  render() {
    const { index } = this.state;
    const { imgArray } = this.props;

    return (
      <div>
        <AutoPlaySwipeableViews index={index} onChangeIndex={this.handleChangeIndex}>
        {    
          imgArray.map(function(item){
            return <img width={800} height={600} src={item}/>;
          }.bind(this))
        }
        </AutoPlaySwipeableViews>
        <Pagination dots={imgArray.length} index={index} onChangeIndex={this.handleChangeIndex} />
      </div>
    );
  }
}

export default ViewAutoPlay;
